.video-modal {
  position: fixed;
  overflow: hidden;
  top: 0;
  left: 0;
  z-index: 3;
  background: rgba(0, 8, 24, 0.37);
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  transition: 0.2s ease;

  &__content {
    background: #fff;
    border-radius: 8px;
    padding: 30px;
    display: grid;
    place-items: center;

    &__header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        margin-bottom: 20px;
    }

    &__video{
        iframe{
            background: #fff;
            box-shadow: 0 0 10px rgb(185 185 185 / 19%);
            border-radius: 8px;
            position: relative !important;
            width: 50vw !important;
            height: 50vh !important;
            display: block;
        }
    }
  }
}
